body {
    color: black;
}

.bxsci {
    font-size: 100px;
    user-select: none;
}

.scioly:hover, .bxsci:hover, .desc:hover {
    user-select: none;
}

.scioly {
    font-size: 100px;
    user-select: none;
    margin-bottom: 20px;
}

.desc {
    font-size: 40px;
    user-select: none;
}

.rotated {
    transform: rotate(-180deg);
}

.show-answer {
    transform: translateY(-40px);
}

.blob-1 {
    height: 500px;
    position: absolute;
    left: -14%;
}

.blob-2 {
    height: 500px;
    position: absolute;
    right: -13%;
    bottom: 10%;
}

.page {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.confetti-1 {
    position: absolute;
    width: 90px;
    left: 5%;
    transform: rotate(-30deg);
}

.confetti-2 {
    position: absolute;
    width: 90px;
    top: 27%;
    left: 10%;
    transform: rotate(50deg);
}