  .slider {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 3;
    margin-bottom: 30px;
  }
  
  .image {
    height: 720px;
    width: 1700px;
  }


  
  
  .slide.active {
    transition-duration: 1s;
    transform: translate(0px);
  }

  .left {
    position: absolute;
    background: black;
    z-index: 4;
    top: 0;
    left: 0;
    height: 720px;
    width: 200px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;
  }

  .left-arrow {
    font-size: 50px;
    color: white;
  }

  .right-arrow {
    font-size: 50px;
    color: white;
  }

  .right {
    position: absolute;
    background: black;
    z-index: 4;
    top: 0;
    right: 0;
    height: 720px;
    width: 200px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;
  }